function sliderMain() {
    var swiper = new Swiper('.main .mySwiper', {
        slidesPerView: 1,
        spaceBetween: 15,
        loop: true,
        effect: "fade",
        autoplay: {
            delay: 2500,
        },
        navigation: {
            nextEl: ".main .swiper-button-next",
            prevEl: ".main .swiper-button-prev",
        },
        pagination: {
            el: ".main .swiper-pagination",
            clickable: true
        },
    })
}

function sliderDirectionsTabsHead() {
    var swiper = new Swiper('.directions__tabs-head .mySwiper', {
        slidesPerView: 'auto',
        spaceBetween: 35,
        slideToClickedSlide: 'true',
        breakpoints: {
            320: {
                spaceBetween: 15,
            },
            993: {
                spaceBetween: 35,
            }
        }
    })
}

let SlidersArray = []
function sliderDirectionsTabsBody() {
    $(".directions__content").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.directions__content[data-slider-id="${index}"] .swiper`, {
            slidesPerView: 1,
            spaceBetween: 15,
            loop: true,
            effect: "fade",
            navigation: {
                nextEl: `.directions__content[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.directions__content[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.directions__content[data-slider-id="${index}"] .swiper-pagination`,
                clickable: true
            },
        })
    })
}

let SlidersArray2 = []
function sliderProducts() {
    $(".products__slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray2[index] = new Swiper(`.products__slider[data-slider-id="${index}"] .mySwiper2`, {
            slidesPerView: 4,
            spaceBetween: 24,
            slideToClickedSlide: 'true',
            navigation: {
                nextEl: `.products__slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.products__slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.products__slider[data-slider-id="${index}"] .swiper-pagination`,
                clickable: true,
                type: "bullets",
            },
            breakpoints: {
                320: {
                    slidesPerView: 1.5,
                    spaceBetween: 10,
                    pagination: {
                        type: "progressbar",
                    },
                },
                577: {
                    slidesPerView: 2.5,
                    spaceBetween: 24,
                    pagination: {
                        type: "progressbar",
                    },
                },
                993: {
                    slidesPerView: 4,
                    spaceBetween: 24,
                    pagination: {
                        type: "bullets",
                    },
                }
            }
        })
    })
}

function sliderCategories() {
    var swiper = new Swiper('.categories .mySwiper', {
        slidesPerView: 4,
        spaceBetween: 15,
        slideToClickedSlide: 'true',
        pagination: {
            el: ".categories .swiper-pagination",
            type: "progressbar",
        },
        breakpoints: {
            320: {
                slidesPerView: 1.5,
            },
            577: {
                slidesPerView: 2.5,
            },
            993: {
                slidesPerView: 4,
            }
        }
    })
}

function sliderPortfolio() {
    var swiper = new Swiper('.portfolio .mySwiper', {
        slidesPerView: "auto",
        spaceBetween: 0,
        slideToClickedSlide: 'true',
        pagination: {
            el: ".portfolio .swiper-pagination",
            type: "progressbar",
        },
        breakpoints: {
            320: {
                slidesPerView: 1.5,
                spaceBetween: 10,
            },
            577: {
                slidesPerView: 2.5,
                spaceBetween: 15,
            },
            993: {
                slidesPerView: "auto",
                spaceBetween: 0,
            }
        }
    })
}

function sliderBreadcrumbs() {
    var swiper = new Swiper('.breadcrumbs .mySwiper', {
        slidesPerView: 'auto',
        spaceBetween: 12,
        slideToClickedSlide: 'true',
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        slidesPerView: "auto",
        spaceBetween: 24,
        freeMode: true,
        watchSlidesProgress: true,
        direction: "horizontal",
        breakpoints: {
            320: {
                spaceBetween: 10,
            },
            401: {
                spaceBetween: 10,
            },
            577: {
                spaceBetween: 24,
            }
        }
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 10,
        slidesPerView: 1,
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
        },
    });
}

function sliderReviews() {
    var swiper = new Swiper('.reviews .mySwiper', {
        slidesPerView: 2,
        spaceBetween: 26,
        navigation: {
            nextEl: ".reviews .swiper-button-next",
            prevEl: ".reviews .swiper-button-prev",
        },
        pagination: {
            el: ".reviews .swiper-pagination",
            type: "progressbar",
        },
        breakpoints: {
            320: {
                slidesPerView: 1.1,
                spaceBetween: 10,
            },
            577: {
                slidesPerView: 1.5,
                spaceBetween: 10,
            },
            993: {
                slidesPerView: 2,
                spaceBetween: 26,
            }
        }
    })
}

$(document).ready(function () {
    sliderMain();
    sliderDirectionsTabsHead();
    sliderDirectionsTabsBody();
    sliderProducts();
    sliderCategories();
    sliderPortfolio();
    sliderBreadcrumbs();
    gallery();
    sliderReviews();


    // Search
    $('.search-btn').click(function () {
        $(this).parents('.search').addClass('search--active')
    })

    $('.search-input').on("keyup", function(e) {
        $(this).siblings('.search-title').removeClass('search-title--active');

        if($(this).val() !== "") {
            $(this).siblings('.search-title').addClass('search-title--active');
        }
    });

    // Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-top').toggleClass('header-top--active')
        $('body').toggleClass('fixed-body')
    })

    // hover slider
    $(".hover__pag-item").mouseover(function() {
        let path = $(this).attr("data-tab-path")
        $(this).parents(".hover__slide").find(".hover__pag-item").removeClass("hover__pag-item--active")
        $(this).addClass("hover__pag-item--active")
        $(this).parents(".hover__slide").find(".hover__img").removeClass("hover__img--active")
        $(this).parents(`.hover__slide`).find(`.hover__img[data-tab-path="${path}"]`).addClass("hover__img--active")
    })

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs-inner").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs-inner").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    // Accordion
    $(".item-show").click((function(){
        $(this).toggleClass("item-show--active"),
        $(this).siblings(".item-hidden").slideToggle()
    }));

    //open-more
    $('.open-more').click(function () {
        $(this).toggleClass('open-more--active')
        $(this).prev('.product-info__group').toggleClass('more-text')
    })

})